import request from '../utils/request'

export function getExchange(data) {
  return request({
    url: '/manage/api/exchange',
    method: 'get',
    data
  })
}
export function updateExchange(data) {
  return request({
    url: '/manage/api/exchange',
    method: 'post',
    data
  })
}

export function postTvWebhook(data) {
  return request({
    url: '/public/api/webhook/tv',
    method: 'post',
    data
  })
}

export function getSpotBalance() {
  return request({
    url: '/manage/api/spot/balance',
    method: 'get'
  })
}
export function getSpotPrices() {
  return request({
    url: '/manage/api/spot/prices',
    method: 'get'
  })
}

export function getUBalance() {
  return request({
    url: '/manage/api/usdt/balance',
    method: 'get'
  })
}

export function getUPosition() {
  return request({
    url: '/manage/api/usdt/position',
    method: 'get'
  })
}

export function getUPrices() {
  return request({
    url: '/manage/api/usdt/prices',
    method: 'get'
  })
}
