<template>
  <div class="index-container">
    <el-button type="primary" size="mini" @click="loadData">刷新</el-button>
<!--     <el-select v-if="token" style="margin: 0 10px;" v-model="selectCoin" filterable placeholder="选择要跑的币">
      <el-option
        v-for="item in allowBases"
        :key="item + '_sel'"
        :label="item"
        :value="item">
      </el-option>
    </el-select> -->
    <el-button type="primary" size="mini" @click="showExchangeSetting = !showExchangeSetting">配置交易所</el-button>
    <el-button type="primary" size="mini" @click="showSetTv = !showSetTv">配置TradingView警报</el-button>
    <template v-if="exInfo.name">
      <h5>合约帐户</h5>
      <el-table
        :data="[uBalance]"
        style="width: 100%"
        >
        <template v-for="item in uBalanceTitles">
          <el-table-column
            :key="item.key"
            :prop="item.key"
            :label="item.title">
            <template slot-scope="scope">
              {{scope.row[item.key] || 0}}
            </template>
          </el-table-column>
        </template>
      </el-table>
      <h5>合约持仓</h5>
      <el-table
        :data="uPositions"
        style="width: 100%"
        >
        <template v-for="item in positionTitles">
          <el-table-column
            :key="item.key"
            :prop="item.key"
            :label="item.title">
            <template slot-scope="scope">
              {{scope.row[item.key] || 0}}
            </template>
          </el-table-column>
        </template>
      </el-table>
      <h5>现货持仓<span style="margin-left: 10px;"><el-checkbox v-model="hideLowSpotValue">隐藏余额小于1U的币种</el-checkbox></span></h5>
      <el-table
        :data="spotPositions.filter(e => !hideLowSpotValue || (e.freeValue + e.lockedValue) > 0.999)"
        style="width: 100%"
        >
        <template v-for="item in spotTitles">
          <el-table-column
            :key="item.key"
            :prop="item.key"
            :label="item.title">
            <template slot-scope="scope">
              <template v-if="item.key == 'freeValue' || item.key == 'lockedValue'">{{scope.row[item.key] && scope.row[item.key] > 0 ? scope.row[item.key].toFixed(5) : 0}}U</template>
              <template v-else>{{scope.row[item.key]}}</template>
            </template>
          </el-table-column>
        </template>
      </el-table>
    </template>
    <template v-else>
      <p>请先配置交易所</p>
    </template>

    <el-dialog title="配置交易所" :visible.sync="showExchangeSetting" :close-on-click-modal="false">
      <el-form :model="exchangeInfo" label-width="90px">
        <el-form-item label="交易所">
          <el-select v-model="exchangeInfo.name" placeholder="请选择交易所">
            <el-option label="Binance" value="binance"></el-option>
            <el-option label="Okx" value="okx"></el-option>
            <el-option label="Huobi" value="huobi"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Key">
          <el-input v-model="exchangeInfo.key"></el-input>
        </el-form-item>
        <el-form-item label="Secret">
          <el-input v-model="exchangeInfo.secret"></el-input>
        </el-form-item>
        <el-form-item v-if="exchangeInfo.name == 'okx'" label="Passphrase">
          <el-input v-model="exchangeInfo.pass"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showExchangeSetting = !showExchangeSetting">取消</el-button>
        <el-button type="primary" @click="setExchange">提交</el-button>
      </div>
    </el-dialog>

    <el-dialog title="配置TradingView警报" :visible.sync="showSetTv" :close-on-click-modal="false">
      <el-form :model="tvConfig" label-width="150px">
        <el-form-item label="帐户类型">
          <el-select v-model="tvConfig.accType" placeholder="请选择帐户类型">
            <el-option label="现货" value="spot"></el-option>
            <el-option label="U本位合约" value="usdt"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="币对">
          <el-select v-model="tvConfig.symbol" filterable placeholder="选择要跑的币">
            <el-option
              v-for="item in tvCoins"
              :key="item.symbol"
              :label="item.symbol"
              :value="item.symbol">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="开仓方向">
          <el-radio-group v-model="tvConfig.action">
            <el-radio-button label="open">{{tvConfig.accType == 'spot' ? '买入' : '开仓'}}</el-radio-button>
            <el-radio-button label="close">{{tvConfig.accType == 'spot' ? '卖出' : '平仓'}}</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="多空方向" v-if="tvConfig.accType != 'spot'">
          <el-radio-group v-model="tvConfig.side">
            <el-radio-button label="long">做多</el-radio-button>
            <el-radio-button label="short">做空</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item :label="'数量(' + (tvConfig.accType == 'usdt' && (exInfo.name == 'okx' || exInfo.name == 'huobi') ? '张' : '币') + ')'">
          <el-input v-model="tvConfig.amount" style="width: 100px;"></el-input>
        </el-form-item>
        <el-form-item label="Webhook URL">
          <el-input v-model="webhook_url" readonly style="width: 400px;"></el-input>
        </el-form-item>
        <el-form-item label="警报消息">
          <el-input v-model="webhook_body" type="textarea" :autosize="{ minRows: 2, maxRows: 10}" readonly style="width: 400px;"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="postTv">实盘请求</el-button>
        <el-button type="primary" @click="showSetTv = !showSetTv">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// huobi
// 53959cff-e7419153-1qdmpe4rty-acd6c
// a10c0aab-12350d84-ebb80209-ecf6f
// okx
// a0245b04-31c7-4a29-afa4-1fb1e696644f
// 8C9D481B1E0C5B7C6AF50A3C69DA20ED
// binance
// 384Iqf2OmQAoG9klyIwXHWALs1MiMrdcownvmuzxYQxEeyib3ca7t1qAVlrpnzN7
// hr0pTuPQzPbor8pEbWpVtrMc5oex8AnEJzaA8q95v0FXIPkxrQZ7G9wuvnVzpkvr

// import * as echarts from 'echarts'
import { getExchange, updateExchange, postTvWebhook, getSpotBalance, getSpotPrices, getUBalance, getUPosition, getUPrices } from '@/api/coin'
// import moment from 'moment'
// const sp = (precent, dot) => {
//   if (!dot) {
//     dot = global.spDot || 5
//   }
//   if (precent && (precent + '').length - (parseInt(precent) + '').length - 1 > dot) {
//     return parseFloat(precent).toFixed(dot)
//   }
//   return precent
// }
const noSplitFunc = (split) => {
    split = split || ''
    return (symbol) => {
        for (let end of ['USDT']) {
            const endVal = split + end
            if (symbol.endsWith(endVal)) {
                const base = symbol.substring(0, symbol.length - endVal.length)
                return {
                    base: base,
                    quote: end
                }
            }
        }
    }
}
const spotSymbolFuncs = {
    'binance': noSplitFunc(),
    'okx': noSplitFunc('-'),
    // 'bybit': noSplitFunc(),
    // 'gateio': noSplitFunc('_'),
    // 'bitget': noSplitFunc(),
    // 'mexc': noSplitFunc(),
    // 'kucoin': noSplitFunc('-'),
    'huobi': noSplitFunc(''),
}
export default {
  name: 'CoinInfo',
  data() {
    return {
      uBalanceTitles: [{
        key: 'currency',
        title: '币种'
      }, {
        key: 'balance',
        title: '权益'
      }, {
        key: 'availableBalance',
        title: '可用余额'
      }, {
        key: 'crossUnPnl',
        title: '浮盈'
      }],
      positionTitles: [{
        key: 'symbol',
        title: '币对'
      }, {
        key: 'entryPrice',
        title: '持仓均价'
      }, {
        key: 'positionSide',
        title: '持仓方向'
      }, {
        key: 'positionAmt',
        title: '持仓数量'
      }, {
        key: 'markPrice',
        title: '标记价'
      }, {
        key: 'unPnl',
        title: '浮盈'
      }],
      spotTitles: [{
        key: 'currency',
        title: '币种'
      }, {
        key: 'free',
        title: '可用数量'
      }, {
        key: 'freeValue',
        title: '可用价值'
      }, {
        key: 'locked',
        title: '锁定数量'
      }, {
        key: 'lockedValue',
        title: '锁定价值'
      }],
      showExchangeSetting: false,
      showSetTv: false,
      exchangeInfo: {
        name: '',
        key: '',
        secret: '',
        pass: ''
      },
      tvWebhook: {
        spot: true
      },
      exInfo: {},
      uBalance: {},
      uPositions: [],
      spotPositions: [],
      spotPrices: {},
      uPrices: {},
      allSpotPrices: [],
      allUPrices: [],
      hideLowSpotValue: true,
      tvConfig: {
        accType: 'spot',
        symbol: '',
        action: 'open',
        side: 'long',
        amount: '0'
      }
    }
  },
  mounted() {
    this.loadExchange()
    setInterval(this.loadData, 5000)
  },
  computed: {
    tvCoins() {
      if (this.tvConfig.accType == 'spot') {
        return this.allSpotPrices
      }
      return this.allUPrices
    },
    webhook_url() {
      return window.location.origin + '/web/public/api/webhook/tv'
    },
    webhook_body() {
      if (this.tvConfig.symbol) {
          const res = {
            alert: true,
            accType: this.tvConfig.accType,
            symbol: this.tvConfig.symbol,
            action: this.tvConfig.action,
            amount: this.tvConfig.amount,
            price: "{{strategy.order.price}}"
          }
          if (res.accType != 'spot') {
            res.side = this.tvConfig.side
          }
          return JSON.stringify(res)
      }
      return '请选择币种'
    }
  },
  watch: {
    spotPrices() {
      this.updateSpotValue()
    },
    uPrices() {
      this.updateUPosition()
    },
    uPositions() {
      this.updateUPosition()
    }
  },
  methods: {
    updateSpotValue() {
      for (let coin of this.spotPositions) {
        const price = this.spotPrices[coin.currency]
        if (price) {
          coin.freeValue = coin.free * price
          // if (coin.freeValue > 0) {
          //   coin.freeValue = coin.freeValue.toFixed(5)
          // }
          coin.lockedValue = coin.locked * price
          // if (coin.lockedValue > 0) {
          //   coin.lockedValue = coin.lockedValue.toFixed(5)
          // }
        }
      }
      this.spotPositions.sort((a, b) => b.freeValue - a.freeValue)
    },
    updateUPosition() {
      for (let pos of this.uPositions) {
        const price = this.uPrices[pos.symbol]
        if (price) {
          pos.markPrice = pos.positionSide == 'LONG' ? price.best_bid : price.best_ask
          pos.unPnl = (pos.markPrice - pos.entryPrice) * pos.positionAmt
        }
      }
    },
    loadExchange() {
      const $this = this
      getExchange().then(resp => {
        if (!resp.data || resp.data.ret != 0) {
          return
        }
        $this.exInfo = {}
        if (resp.data.data.name) {
          $this.exInfo.name = resp.data.data.name
          $this.loadData()
        }
      })
    },
    loadData() {
      if (!this.exInfo.name) {
        return
      }
      const $this = this
      getSpotBalance().then(resp => {
        if (!resp.data || resp.data.ret != 0) {
          return
        }
        $this.spotPositions = resp.data.data
        $this.updateSpotValue()
      })
      getUBalance().then(resp => {
        if (!resp.data || resp.data.ret != 0) {
          return
        }
        $this.uBalance = resp.data.data
      })
      getUPosition().then(resp => {
        if (!resp.data || resp.data.ret != 0) {
          return
        }
        $this.uPositions = resp.data.data.positions
      })
      getUPrices().then(resp => {
        if (!resp.data || resp.data.ret != 0) {
          return
        }
        $this.uPrices = {}
        $this.allUPrices = resp.data.data
        for (let price of resp.data.data) {
          $this.uPrices[price.symbol] = price
        }
      })
      getSpotPrices().then(resp => {
        if (!resp.data || resp.data.ret != 0) {
          return
        }
        $this.spotPrices = {
          'USDT': 1,
          'USDC': 1
        }
        const func = spotSymbolFuncs[this.exInfo.name]
        $this.allSpotPrices = resp.data.data
        for (let price of resp.data.data) {
          const coin = func(price.symbol)
          if (coin) {
            $this.spotPrices[coin.base] = price.best_bid
          }
        }
      })
    },
    setExchange() {
      const $this = this
      updateExchange(this.exchangeInfo).then(resp => {
        $this.showExchangeSetting = false
        if (resp.data.ret == 0) {
          $this.$message({
            message: '更新交易所成功',
            type: 'success'
          })
          $this.loadExchange()
        } else {
          $this.$message({
            message: '更新交易所失败',
            type: 'error'
          })
        }
      })
    },
    postTv() {
      const array0 = (arr) => {
        if (arr && arr.length > 0) {
          return arr[0]
        }
      }
      const $this = this
      const data = Object.assign({}, JSON.parse(this.webhook_body))
      if (data.accType == 'spot') {
        data.price = array0(this.allSpotPrices.filter(e => e.symbol == data.symbol).map(e => data.action == 'open' ? e.best_ask : e.best_bid))
      } else {
        data.price = array0(this.allUPrices.filter(e => e.symbol == data.symbol).map(e => (data.action == 'open' && data.side == 'long') || (data.action == 'close' && data.side == 'short') ? e.best_ask : e.best_bid))
      }
      if (!data.price) {
        $this.$message({
          message: '找不到币对的价格信息，请刷新重试看看',
          type: 'error'
        })
        return
      }
      postTvWebhook(data).then(resp => {
        $this.showSetTv = false
        if (resp.data && resp.data.ret == 0) {
          $this.$message({
            message: '请求成功',
            type: 'success'
          })
          $this.loadData()
        } else {
          $this.$alert('报错:' + JSON.stringify(resp.data.data), '请求失败')
        }
      })
    },
  }
}
</script>

<style lang="scss" scoped>
$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;
$primary_color: #0143cc;
